import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';

import PubNubReact from 'pubnub-react';

const styles = {
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
};

class App extends Component {

  constructor(props) {
    super(props);
    this.pubnub = new PubNubReact({
        publishKey: 'pub-c-af9e408a-d4a8-473c-b591-81402cdf9aaf',
        subscribeKey: 'sub-c-7e76d5bc-2658-11e9-9508-c2e2c4d7488a'
    });
    this.pubnub.init(this);
  }

  sendColour = (bgColour) => {
    this.pubnub.publish({
        message: {colour: bgColour},
        channel: 'colour-demo'
    });
  }

  render(){
    const { classes } = this.props;
    return(
      <Card className={classes.card}>
            <Button size="small" color="primary" onClick={() => this.sendColour("#D61E56")}>
              Red
            </Button>
            <Button size="small" color="primary" onClick={() => this.sendColour("#08DACB")}>
              Blue
            </Button>
        </Card>
      );
    }
}

const ChatComponent = withStyles(styles)(App);

ReactDOM.render(<ChatComponent />, document.getElementById('root'));


